@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@700&display=swap');

.Rheader {
    width: 100%;
    height: 15%;
    display: flex;
    flex-direction: row;
    justify-content: left;
}

.profile {
    display: flex;
    justify-content: left;
    width: 150px;
    height: 100%;
    object-fit: cover;
    /* 이미지가 넘치는 부분을 잘라냄 */
    border-radius: 50%;
    /* 이미지를 원형으로 만듦 */
}

.intro {
    margin-left: 4%;
}

.name {
    font-size: 3em;
    font-weight: bold;
}

.intro > .introMsg {
    font-family: 'Noto Sans KR', sans-serif;
}

.intro > .name, .intro > a {
    font-family: 'Noto Serif KR', serif;
}

@media (max-width: 768px) {
    .Rheader {
        flex-direction: column;
        justify-content: left;
        margin-bottom: 30%;
    }
    .profile {
        width: 100px;
        height: 100px;
    }
    .intro {
        margin-left: 0;
        text-align: left;
    }
    .name {
        font-size: 2.5em;
    }
    .gitLink {
        font-size: 0.7em;
    }
}