@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300&display=swap');

.Rbody {
    width: 100%;
    height: 85%; /*전체 높이에서 헤더 높이 뺀 값*/
    margin-top: 5%;
    margin-bottom: 5%;
}

.AboutMe > div:first-child > span, .projects > div:first-child >span, .skills > div:first-child >span, .education > div:first-child >span, .reachingMe > div:first-child >span {
    font-family: 'Josefin Sans', sans-serif;
  }

.AboutMe, .projects, .skills, .education, .reachingMe {
    display: flex;
    flex-direction: row;
    margin-bottom: 6%;
}

.introText {
    margin-left: 7.5%;
}

.skillIntroText {
    margin-left: 9%;
}

.educationIntroText {
    margin-left: 6.6%;
}

.reachingMeIntroText {
    margin-left: 5.8%;
}

.introText span, .skillIntroText span, .educationIntroText span, .reachingMeIntroText span {
    line-height: 2;
    font-family: 'Noto Sans KR', sans-serif;
}

.projects, .skills, .education, .reachingMe {
    margin-top: 4%;
}

a {
    color: inherit;
    text-decoration: none;
}

a:hover, a:active {
    color: aqua;
}

a, .title {
    font-size: 18px;
    font-weight: bold;
}

li {
    font-family: 'Noto Sans KR', sans-serif;
}

@media (max-width: 768px) {
    .AboutMe, .projects, .education, .skills, .reachingMe {
        display: flex;
        flex-direction: column;
        justify-content: left;
        align-items: left;
        margin-bottom: 10%;
    }
    .introText, .skillIntroText, .educationIntroText, .reachingMeIntroText {
        margin-left: 0;
    }
    .title1 {
        margin-bottom: 4%;
    }
    a, .title {
        font-size: 17px;
        font-weight: bold;
    }
}
