.main {
    width: 94%;
    height: 100vh; /*뷰포트의 높이를 100%로 설정*/
    margin-left: 6%;
    margin-top: 5%;
}


@media (max-width: 768px) {
    .main {
        width: 100%;
        margin: 4%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: left;
    }
}
